import Vue from 'vue';
import VueRouter from 'vue-router';

import store from '@/store/index';

import MainLayout from '@/components/layout/MainLayout.vue';
import Industry from '@/views/Industry.vue';
import IndustryItem from '@/views/IndustryItem.vue';
import IndustryDetail from '@/views/IndustryDetail.vue';
Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: MainLayout,
    children: [
      {
        path: '/',
        name: 'Industry',
        component: Industry,
      },
      {
        path: '/item/:code',
        name: 'IndustryItem',
        component: IndustryItem,
      },
      {
        path: '/detail/:code/:subCode',
        name: 'IndustryDetail',
        component: IndustryDetail,
      },
    ],
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (store.state.list && store.state.list.length) {
    next();
  } else {
    store
      .dispatch('fetchData')
      .then(() => {
        next();
      })
      .catch((e) => {
        console.error(e);
      });
  }
});

export default router;
