import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import cyrillicToTranslit from 'cyrillic-to-translit-js';

const config = {
  headers: { 'Access-Control-Allow-Origin': '*' },
  crossdomain: true,
};

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    list: [],
  },

  getters: {
    list(state) {
      const normalizeKey = (key) => {
        return cyrillicToTranslit()
          .transform(key, '-')
          .replace(/,/g, '');
      };
      return state.list.reduce((acc, item, index) => {
        const key = item['1 категория'];
        const childKey = item['2 категория'];
        const name = item['Название новое'];
        const desc = item['Описание новое'];
        const price = item['Цена'];
        const keyTrasnlit = normalizeKey(key);
        const childKeyTrasnlit = normalizeKey(childKey);
        if (acc[keyTrasnlit]) {
          const subCat = acc[keyTrasnlit].children.find(
            (item) => item.label === childKey
          );
          if (subCat) {
            subCat.child.push({
              path: `/detail/${childKeyTrasnlit}/${normalizeKey(name)}`,
              label: name,
              desc,
              price,
              code: `${normalizeKey(name)}`,
            });
          } else {
            acc[keyTrasnlit].children.push({
              label: childKey,
              code: childKeyTrasnlit,
              path: `/item/${childKeyTrasnlit}`,
              child: [
                {
                  label: name,
                  code: `${normalizeKey(name)}`,
                  path: `/detail/${childKeyTrasnlit}/${normalizeKey(name)}`,
                  desc,
                  price,
                },
              ],
            });
          }
        } else {
          acc[keyTrasnlit] = {
            id: index,
            label: key,
            code: keyTrasnlit,
            path: `/item/${keyTrasnlit}`,
            children: [
              {
                label: childKey,
                code: childKeyTrasnlit,
                path: `/item/${childKeyTrasnlit}`,
                child: [
                  {
                    label: name,
                    code: `${normalizeKey(name)}`,
                    path: `/detail/${childKeyTrasnlit}/${normalizeKey(name)}`,
                    desc,
                    price,
                  },
                ],
              },
            ],
          };
        }
        return acc;
      }, {});
    },
  },
  mutations: {
    setList(state, payload) {
      state.list = payload;
    },
  },
  actions: {
    fetchData({ commit }, payload) {
      return axios.get('/data.json', config).then((res) => {
        commit('setList', res.data.data);
      });
    },
  },
  modules: {},
});
