<template>
  <div style="oveflow-x: hidden">
    <el-row>
      <el-col :md="12">
        <el-tree
          :data="splitList[0]"
          @node-click="handleNodeClick"
          accordion
          :expand-on-click-node="false"
        ></el-tree>
      </el-col>
      <el-col :md="12">
        <el-tree
          :data="splitList[1]"
          @node-click="handleNodeClick"
          accordion
          :expand-on-click-node="false"
        ></el-tree
      ></el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'IndustryList',

  components: {},

  props: {
    list: {
      type: Object | Array,
    },
    title: {
      type: String,
    },
  },

  computed: {
    splitList() {
      const list = Array.isArray(this.list)
        ? this.list
        : Object.values(this.list);
      const middle = Math.round(list.length / 2);
      return [list.slice(0, middle), list.slice(middle)];
    },
  },

  methods: {
    handleNodeClick(item) {
      this.$router.push(item.path);
    },
  },

  mounted() {},
};
</script>
