        let res = [];

        const iter = () => {
          if (!item.children) {
            res.push(item);
            console.log(res);
            // return;
          } else {
            item.children.forEach((child) => {
              // console.log(child);
              iter(child);
            });
          }
        };
        iter();
        return res;
      };

      const child = this.nodeByCode.children
        ? this.nodeByCode.children
