export default (tree, code) => {
  const normalizeTree = Array.isArray(tree) ? tree : Object.values(tree);
  let res = null;
  console.log(tree, code);
  const iter = (item) => {
    if (item.code === code) {
      res = item;
      return;
    }
    if (item.children)
      item.children.forEach((item) => {
        iter(item);
      });
    if (item.child)
      item.child.forEach((item) => {
        iter(item);
      });
  };

  normalizeTree.forEach((item) => {
    iter(item);
  });

  return res;
};
